import React from 'react';
import { Card, Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import Footer from '../../Shared/Footer/Footer';
import MessengerChat from '../../Shared/MessengerChat/MessengerChat';
import TopSection from '../../Shared/TopSection';
import AdSense from '../../../AdSense';
import AdSenseCardAdds from '../../../AdsenseAddsCard/AdSenseCardAdds';

const NonePadded = () => {
    const {allProduct} = UseAuth()
    const nonPadded =  allProduct?.filter(function(product) {
        return product.category === "Non Padded";
      });
    return (
        <div>
            <AdSense/>
            <TopSection/>
             <div>
         <Container>
         <Row xs={2} md={4} className="g-4">
              {nonPadded?.map((nonPadded,index) => (
                  <Col key={index}>
                 <Card className='product-card'>
                    <Link to={`/details/${nonPadded._id}`} className='order-link'>
                        <Card.Img variant="top" src={nonPadded.img} className="img-fluid" />
                    </Link> 
                            <div className='text-start product-size pb-2'>
                            <div>
                                 <div className='card-price'>
                                    <p>Available size:</p><p className='bdt-show'>&#2547; {nonPadded?.price}</p>
                                </div> 
                                 <div className='size-box'>
                                 {
                                 nonPadded.size.map((sizes,index)=><span key={index}>{sizes}</span>)
                                 }
                                 </div>
                             </div>
                             <button className='buynow-btn mt-2'><Link to={`/details/${nonPadded._id}`} className='order-link'>Order Now</Link></button>
                            </div>
                                 {/* <button className='addtoCart mt-2'>Add to Cart</button> */}
                                
                        </Card>
                  </Col>
              ))}
              </Row>

               {/* Google adds// */}
               <AdSenseCardAdds/>
         </Container>
          </div>
          <Footer/>
          <MessengerChat/>
        </div>
    );
};

export default NonePadded;