import React, { useState } from 'react';
import { useEffect } from 'react';
import { Card, Container, ProgressBar } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import swal from 'sweetalert';
import UseAuth from '../../Hooks/UseAuth';

const AddBannerText = () => {
  const {setNum}= UseAuth();
  const [selectedFile, setSelectedFile] = useState();
  const [uploadProgress, setUploadProgress] = useState(0);

    const [Banner,setBanneer] = useState();
    const [now,setnow] = useState(100);
    const [BannerId,setBannerId] = useState(null);
    const {register, handleSubmit,reset} = useForm();

  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);
  
    // Create a new FormData object
    const formData = new FormData();
    formData.append('image', file);
  
    // Make a POST request to the backend endpoint for file upload
    fetch('https://api.femmewearbd.com/uploadProductImg', {
      method: 'POST',
      body: formData,
      // Report upload progress
      onUploadProgress: (progressEvent) => {
        const progress = Math.round(
          (progressEvent.loaded / progressEvent.total) * 100
        );
        setUploadProgress(progress);
      }
      
    })
      .then((response) => response.json())
      .then((data) => {
        
        //console.log('File uploaded successfully', data);
        const Imgurl = data.imageUrl;
        // const filename = Imgurl.substring(Imgurl.lastIndexOf('/') + 1);
        // get image url after upload
        // const url = `https://api.femmewearbd.com/uploadProductImg/${filename}`;
        setBanneer(Imgurl)
        //console.log(url)
      })
      .catch((error) => {
        console.error('Error uploading file', error);
  
        // Handle error if the file upload fails
      });
      //console.log(selectedFile);
      event.preventDefault() 
  };




useEffect(()=>{
  fetch('https://api.femmewearbd.com/banner')
  .then(res=>res.json())
  .then(data=> setBannerId(data[0]._id))
},[]);

    const onSubmit = data => {
      // ---------------delete previous data-------//
      const url = `https://api.femmewearbd.com/baner/${BannerId}`;
      fetch(url,{
          method:'DELETE'
      })
      .then(res=>res.json())
      .then(data=>{
          if(data.acknowledged){
              setNum(num=>num+1)
              swal("success! Product has been deleted!", {
                  icon: "success",
                });
          }
      })
  
  // --------------------add banner info to the data bease// 
  const bannerInfo = {
        textLineOne:data?.textOne,
        textLineTow:data?.textTow,
        textLineThree:data?.textThree,
        banner:Banner
      }

      fetch("https://api.femmewearbd.com/update_banner", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(bannerInfo),
    })
    .then(res=> res.json())
    .then(result =>{
      if(result.acknowledged){
        setNum(num=>num+1)
        swal("Success!", "Banner Successfully Update", "success");
        setnow(0)
        reset();
    }
    } )
    };

    return (
        <div className='dashboard-page'>
            <Container>
                <Card className='m-md-4 m-2'>
                    <h3>Change Banner image</h3>
                    <form onSubmit={handleSubmit(onSubmit)}>
                    <input type='file' {...register("img",{required:true})}
                        className='add-in-field'
                        onChange={(e) => handleImageUpload(e)}
                        />
                        <div className='add-in-field mx-auto'>
                          {
                            selectedFile||Banner?<ProgressBar now={now} label={`${now}%`} />:<ProgressBar now={uploadProgress} label={`${0}%`} />
                          }
                        </div>
                        <div className='my-4'>
                          <h5>Banner text Line 1</h5>
                          <input type='text' {...register("textOne",{required:true})}
                          className='add-in-field'
                          placeholder='not more 4 word'
                        />
                          <h5>Banner text Line 2</h5>
                          <input type='text' {...register("textTow",{required:true})}
                          className='add-in-field'
                          placeholder='not more 4 word'
                        />
                          <h5>Banner text Line 3</h5>
                          <input type='text' {...register("textThree",{required:true})}
                          className='add-in-field'
                          placeholder='not more 4 word'
                        />
                        </div>

                        <input className='submite-btn' type="submit" />
                    </form>
                </Card>

            </Container>
           
        </div>
    );
};

export default AddBannerText;