import React from 'react';
import './Banner.css';
import UseAuth from '../../Hooks/UseAuth';

const Banner = () => {
 const {banner} = UseAuth();
    return (
        <div>
          <header>
             <div className='banner-section'>
             <img className='banner-img' src={banner?.banner} alt="femmewear-product" />
           
                <div className='banner-heading'>
                    <p className='primary-heading'>
                      {banner?.textLineOne}<br />
                      {banner?.textLineTow}<br />
                      {banner?.textLineThree} <br />
                      {/* Support Bra <br /> */}
                    </p>
                </div>
                {/* <button className='responsive-button'>  
                <Link to="/shop"> See More </Link>
                 </button> */}

             </div>
          </header>
        </div>
    );
};

export default Banner;