import React from 'react';
import TopSection from '../../Shared/TopSection';
import { Card, Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import Footer from '../../Shared/Footer/Footer';
import MessengerChat from '../../Shared/MessengerChat/MessengerChat';
import AdSense from '../../../AdSense';
import AdSenseCardAdds from '../../../AdsenseAddsCard/AdSenseCardAdds';
const Skirts = () => {
    const {allProduct} = UseAuth()
    const Skirts =  allProduct.filter(function(product) {
        return product.category === "Skirts";
      });

    return (
        <div>
            <AdSense/>
               <TopSection/>
             <div>
         <Container>
         <Row xs={2} md={4} className="g-4">
              {Skirts?.map((skirts,index) => (
                  <Col key={index}>
                 <Card className='product-card pb-2'>
                 <Link to={`/details/${skirts._id}`} className='order-link'>
                        <Card.Img variant="top" src={skirts.img} className="img-fluid" />
                    </Link>
                            <div className='text-start product-size'>
                            <div>
                                <div className='card-price'>
                                    <p>Available size:</p><p className='bdt-show'>&#2547; {skirts?.price}</p>
                                </div>
                                 <div className='size-box'>
                                    {
                                      skirts.size.map((sizes,index)=><span key={index}>{sizes}</span>)
                                     }
                                 </div>
                            </div>
                            <button className='buynow-btn mt-2'><Link to={`/details/${skirts._id}`} className='order-link'>Order Now</Link></button>
                            </div>
                                 {/* <button className='addtoCart mt-2' onClick={()=>addItem(braset)}>Add to Cart</button> */}
                                 
                        </Card>
                  </Col>
              ))}
              </Row>


               {/* Google adds// */}
               <AdSenseCardAdds/>
         </Container>
          </div>
          <Footer/>
          <MessengerChat/>
        </div>
    );
};

export default Skirts;