import React from 'react';
import TopSection from '../Shared/TopSection';
import { Container } from 'react-bootstrap';
import './NotFound404.css'
import { NavLink } from 'react-router-dom';

const NotFound4O4 = () => {
   
    return (
        <div>
            <TopSection/>
            <Container>
                <div className='pt-5'>
                    <p className="not-Found">(;-;)</p>
                    <h3 className='text-start'>
                    Oops! We Couldn't Find That Page (Error 404)
                    </h3>
                    <div className='text-start'>
                    <h4 className='text-primary'>But We're Here to Help...</h4> 
                    <h4 className='text-primary'>Here are some options to get you back on track:</h4>
                    <div className='py-5'>
                        <NavLink to="/"><button className='btn btn-success me-3'>Home</button></NavLink>
                        <NavLink to="/shop"><button className='btn btn-success me-3'>Shop</button></NavLink>
                        <NavLink to="/Blog"><button className='btn btn-success me-3'>Blog</button></NavLink>
                        <NavLink to="/SizeCalculator"><button className='btn btn-success me-3'>Size Calculator</button></NavLink>
                    </div>

                    </div>

                    <p className='text-start'>Also you can navigate any pages from navbar</p>

                </div>
            </Container>
            
        </div>
    );
};

export default NotFound4O4;