import React from 'react';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import logo from '../../../Assets/femmlogo4.png';
import './Searchbar.css'
import { Link } from 'react-router-dom';
import { useCart } from 'react-use-cart';
import UseAuth from '../../Hooks/UseAuth';
import { useState } from 'react';


const Searchbar = () => {
    const {totalUniqueItems } = useCart();
     const [match, setMatched] = useState([]);
    const {setSearchProduct} = UseAuth();
    
    const searchHandler = async (searchText) => {
        const url = 'https://api.femmewearbd.com/allproducts';
        const response = await fetch(url);
        const data = await response.json();
        setMatched(data);
      
        const matchedProduct = match?.filter(
          (product) =>
            product.name.toLowerCase().includes(searchText.toLowerCase()) ||
            product.size.some((allsize) => allsize.toLowerCase().includes(searchText.toLowerCase())
            )
        );
        setSearchProduct(matchedProduct);
      };

    return (
        <div className='search-bar'>
            <Container>
            <Row className='m-0'>
                <Col sm="12" md="4">
                   <div className='logo'>
                      <Link to='/'>
                        <img className='inner-logo' src={logo} alt="femmewear-product" />
                      </Link>   
                      <Link to='/'>  
                          <p className='logo-name mt-3'>Femme Wear BD</p>
                     </Link>
                 </div>
                  
                </Col>
                <Col sm="12" md="6" className='inputCartWraper'>
                   <div className='search-cart'>
                        <div className='home-searchbar'>
                           <Link to ='/shop' className='w-100'>
                            <input 
                            className='search-input-home' 
                            type="text" 
                            placeholder='Search Now' 
                            onChange={(e)=>searchHandler(e.target.value)}/> </Link>
                            <i className="fa-solid fa-magnifying-glass search-icon"></i>
                        </div>
                        <div className='cart-section'>
                                <Link to="/myCart"><i className="fa-solid fa-cart-shopping"></i></Link>
                                <span className="cart-counter">{totalUniqueItems}</span>
                                {/* myCart */}
                        </div>
                   </div>
                </Col>
                <Col sm="12" md="2"> 
                </Col>
            </Row>
            </Container>
        </div>
    );
};

export default Searchbar;