import React from 'react';
import { Card, Col, Row } from 'react-bootstrap';
import { useFieldArray, useForm } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import './UpdateProduct.css'

const UpdateProduct = () => {
    const {updateProductId} = useParams();
    const {allProduct,setNum}= UseAuth();

    const updateProduct = allProduct?.find((product)=> product?._id === updateProductId);
    
        const {register, handleSubmit,control,reset} = useForm();
        const onSubmit = (data) =>{
            const size = data.sizeObject?.map(value=>{return value.value});
            const product = {
                category:updateProduct.category,
                description:updateProduct.description,
                img:updateProduct.img,
                name:updateProduct.name,
                objectType:updateProduct.objectType,
                price:updateProduct.price,
                id:updateProduct.id,
                stock:data.stock,
                size:size,
                newPrice:data.newPrice,
                }

                //---------------------- delete old product
                // ------------------------------------------------
                const url = `https://api.femmewearbd.com/allproducts/${updateProduct._id}`;
                fetch(url,{
                    method:'DELETE'
                })
                .then(res=>res.json())
                .then(data=>{
                    if(data.acknowledged){
                        setNum(num=>num+1)
                       
                    }
                })
                   //----------------------update product and post new product
                // ------------------------------------------------
                fetch("https://api.femmewearbd.com/allproducts", {
                    method: "POST",
                    headers: {
                        "content-type": "application/json",
                    },
                    body: JSON.stringify(product),
                    })
                    .then(res=> res.json())
                    .then(result =>{
                    if(result.acknowledged){
                        setNum(num=>num+1)
                        alert('Product Successfully Updated');
                        reset()
                    }
                    } )

        }
        const { fields, append, remove} = useFieldArray({
            control, // control props comes from useForm (optional: if you are using FormContext)
            name: "sizeObject", // unique name for your Field Array
          });
    return (
        <div className='dashboard-page'>
            <div>
                <Row className='m-0'>
                    <Col className='col-md-6'>
                        <div className='update-img'>
                          <img className='img-fluid' src={updateProduct?.img} alt=''/>
                        </div>
                    </Col>
                    <Col  className='col-md-6'>
                        <Card>
                            <div>
                                <form onSubmit={handleSubmit(onSubmit)}> <br />
                                    <div className='product-add-form'>
                                        <div>
                                        <span className='float-start'>
                                            Available in Stock:<b>{updateProduct?.stock}</b></span> <br/>
                                        <input 
                                            type='number' {...register("stock",{required:true})} 
                                            placeholder='Add new stoct'
                                            className='update-product-field'
                                         />
                                        <br/>
                                        <br/>
                                        </div>
                                        <span className='float-start'>
                                            Add New Price <b>(optional)</b></span>
                                        <input type='number' {...register("newPrice",{required:false})} 
                                            placeholder='Add new price '
                                            className='update-product-field'
                                         />
                                         <div className='multi-form'>
                                            <span>Available size:</span> <br />
                                            {fields.map((field, index) => (
                                            <input
                                            key={field.id} // important to include key with field's id
                                            className='size-field'
                                            {...register(`sizeObject.${index}.value`,{required:true})} 
                                             />
                                                 ))}  
                                        <button 
                                        type='button'
                                        onClick={()=>append()}
                                        className='add-btn' 
                                        > +</button>          
                                        </div>
                                        <div className="size-clear-btn-container">
                                        <button type='button' className='size-clear-btn' onClick={()=>remove()}> Remove Size</button>
                                        </div>
                                        
                                        <input type="submit" className='submite-btn'/>
                                    </div>
                                </form>
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>
          
        </div>
    );
};

export default UpdateProduct;