import React from 'react';
import { Card, Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import Footer from '../../Shared/Footer/Footer';
import MessengerChat from '../../Shared/MessengerChat/MessengerChat';
import TopSection from '../../Shared/TopSection';
import AdSense from '../../../AdSense';
import AdSenseCardAdds from '../../../AdsenseAddsCard/AdSenseCardAdds';

const Padded = () => {
  const {allProduct} = UseAuth()

    const padded =  allProduct?.filter(function(product) {
        return product.category === "Padded";
      });
    return (
        <div>
          <AdSense/>
              <TopSection/>
            <div>
            <Container>

               {/* Google adds// */}
               <AdSenseCardAdds/>

              <Row xs={2} md={4} className="g-1 m-0">
                    {padded?.map((padded,index) => (
                     <Col key={index}>
                      <Card className='product-card'>
                      <Link to={`/details/${padded._id}`} className='order-link'>
                         <Card.Img variant="top" src={padded.img} className="img-fluid" />
                      </Link> 
                            <div className='text-start product-size pb-2'>
                            <div>
                                <div className='card-price'>
                                    <p>Available size:</p><p className='bdt-show'>&#2547; {padded?.price}</p>
                                </div>
                                 <div className='size-box'>
                                 {
                                 padded.size.map((sizes,index)=><span key={index}>{sizes}</span>)
                                 }
                                 </div>
                             </div>
                             <button className='buynow-btn mt-2'><Link to={`/details/${padded._id}`} className='order-link'>Order Now</Link></button>
                            </div>
                                 {/* <button className='addtoCart mt-2'>Add to Cart</button> */}
                                
                        </Card>
                      </Col>
                     ))}
                </Row>

                {/* Google adds// */}
                <AdSenseCardAdds/>

            </Container>
          </div>
          <Footer/>
          <MessengerChat/>
        </div>
    );
};

export default Padded;

