import React from 'react';
import { Card, Container} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import { Swiper, SwiperSlide} from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import {Keyboard, Pagination, Navigation,Autoplay } from "swiper";
const MenstrualCollection = () => {
    const {allProduct} = UseAuth();
    const Menstruales =  allProduct.filter(function(product) {
        return product.category === "Menstrual Cup";
      });
    //  
    return (
        <>
    <div>
        <div className='display-section'>
            <Link to="/menstrual">
               <h3>Women Necessary</h3>
            </Link> 
         </div>
       <div>
         <Container>
         <Swiper
              slidesPerView={4}
              slidesPerGroupSkip={1}
              grabCursor={true}
              centeredSlides={false}
              spaceBetween={30}
              loop={true}
              pagination={{ clickable: true}}
              keyboard={{enabled: true}}
              autoplay={{delay: 5000, disableOnInteraction: true,}}
              navigation={true}
              breakpoints={{
                  240: {
                  slidesPerView: 2,
                  spaceBetween: 20,
                  slidesPerGroup: 2,
                  },
                  600: {
                  slidesPerView: 3,
                  slidesPerGroup: 3,
                  spaceBetween: 40,
                  },
                  800: {
                  slidesPerView: 4,
                  spaceBetween: 40,
                  slidesPerGroup: 4,
                  },
                  1024: {
                  slidesPerView: 5,
                  spaceBetween: 30,
                  slidesPerGroup: 5,
                  },          
              }}
              modules={[Keyboard, Pagination, Navigation, Autoplay]}
            className="mySwiper"
        > 
              {Menstruales?.map((Menstrual, index) => (
                    <SwiperSlide key={index}>
                        <Card className='product-card'>
                            <Link to={`/details/${Menstrual._id}`} className='order-link'>
                            <Card.Img variant="top" src={Menstrual.img} className="img-fluid home-slide-image" alt="femmewearbd image" />
                            <p className='home-slide-product-name'>{Menstrual.name}</p>
                            </Link>
                        </Card>
                    </SwiperSlide>   
              ))}
        </Swiper>   
        <Link to="/menstrual">
          <button className='see-more'>See More</button>  
          </Link>  
         </Container>
          </div>
      </div>
    </>
    );
};

export default MenstrualCollection;