import React from 'react';
import Footer from '../Shared/Footer/Footer';
import MessengerChat from '../Shared/MessengerChat/MessengerChat';
import brasizeBanner from '../../Assets/bra_Size_banner.jpg'
import sizeList from '../../Assets/sizelist.jpg'
import TopSection from '../Shared/TopSection';
import './SizeCalculator.css'
import { Col, Container, Row } from 'react-bootstrap';
import { useState } from 'react';
import { useEffect } from 'react';
import AdSense from '../../AdSense';
import AdSenseCardAdds from '../../AdsenseAddsCard/AdSenseCardAdds';

const SizeCalculator = () => {
    const [bust,setBust] = useState();

    const [band,setBand] = useState();
    const [outPutband,setOutputBand] = useState(0);
    const [cup,setCup] = useState(null);

    useEffect(()=>{
        if(band % 2 === 0){
            const size = band + 4
            setOutputBand(size)
        }
        if(band % 2 !== 0){
            setOutputBand(Math.round(band)+5)
        }
        const subtract =  bust - outPutband;
        if(subtract<2){
            setCup('AA')
        }
        if(subtract===1){
            setCup('A')
        }
        if(subtract===2){
            setCup('B')
        }
        if(subtract===3){
            setCup('C')
        }
        if(subtract===4){
            setCup('D')
        }
        if(subtract===5){
            setCup('DD/E')
        }
        if(subtract===6){
            setCup('DDD/F')
        }
        if(subtract===7){
            setCup('DDDD/G')
        }
        if(subtract===8){
            setCup('H')
        }
        if(subtract > 8){
            setCup('Oops! Something is wrong. Please check your measurements again')
        }
        
    },[band,cup,bust,outPutband])
   

    return (
        <div>
            <AdSense/>
             <TopSection/>
            <Container>

                 {/* Google adds// */}
                 <AdSenseCardAdds/>

            <div className='calculator-banar-section'>
            <p className='fs-4 bg-danger text-white mt-4 px-2'>তা হলে চলো জেনে নিই কিভাবে সঠিক সাইজের ব্রা নির্ধারন করা যায়</p>
                <img className='w-100' src={brasizeBanner} alt="femmewear-product" />
            </div>
            <Row>
                <Col className='col-12 col-md-8 text-start'>
                <div>
                
                <p>এজন্য  আমাদের প্রয়োজন একটি মেজারমেণ্ট টেপ।</p>
                <p>সঠিক এবং নির্ভুল সাইজের ব্রা এর জন্য বের করতে হবে :- 
                 <ul>
                    <li>১) BAND SIZE  এবং</li>
                    <li>২) BUST SIZE</li>
                 </ul>
                </p>
                <h3>BAND SIZE :</h3>
                 <ul>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                      ব্যান্ড সাইজ বের করার জন্য স্তনের নিচ বরাবর মেজারমেন্ট টেপ দিয়ে ইঞ্চিতে মেপে নাও।
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    যদি সংখ্যাটি জোড় হয় তার সাথে 4 যোগ করে যে সংখ্যাটি পাবে সেটিই হবে তোমার BAND SIZE।
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    ধরো সংখ্যাটি 30 তাহলে BAND SIZE: 30 + 4 = 34
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    আর সংখ্যাটি যদি বিজোড় হয়, তবে তার সাথে 5 যোগ করো।
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    ধরো সংখ্যাটি 29 তা হলে BAND SIZE  : 29 + 5 = 34  <br/> 
                    অত এব তোমার BAND SIZE 34
                    </li>
                 </ul>
                 <h3>BUST SIZE :</h3>
                 <ul>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                       BUST SIZE এর মাপ নেওয়ার সময় ভারী বা মোটা ফোমের ব্রা পড়া যাবে না। 
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    প্রথমে মেজারমেন্ট টেপ দিয়ে স্তনের ঠিক উপরে আলতো ভাবে ধরো ।
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    মনে করো সংখ্যাটি হলো 36 ইঞ্চি , তাহলে এটাই তোমার BUST SIZE 
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    এখন BUST SIZE থেকে BAND SIZE বিয়োগ করো , <br/> 
                    অর্থাৎ, <b>BUST SIZE  36 - BAND SIZE 34 = 2</b> 
                    </li>
                    <li style={{listStyle:"default", listStyleType:"disc"}}>
                    এই বিয়োগফলই হলো তুমার কাঙ্খিত কাপ সাইজ , আর কাপ সাইজ সিস্টেম অনুযায়ী 2 মানে B <br />
                    <b>অত এব ৩৪ ব্যান্ড সাইজের B কাপটি তোমার জন্য সঠিক এবং উপযুক্ত ব্রা (34B)</b>
                    </li>
                    
                 </ul>
                </div>

                    {/* Google adds// */}
                    <AdSenseCardAdds/>

               </Col>
                <Col className='col-12 col-md-4 text-start'>
                 <img className='img-fluid mt-4' src={sizeList} alt="femmewear-product" />
               </Col>
               <Col>
               </Col>
            </Row>
            <div className='calculator'>
                <div>
                   <span className='fs-4'> Enter Underbust: </span>
                     <input 
                    className='calculator-input'
                    min="23" max="44"
                    type='number'
                    onChange={(e)=>setBand(Math.round(e.target.value))}
                    />
                </div>
                <div>
                    <h3 className='fw-bold'>Your</h3>
                    <h1 className='text-success fw-bold'>BRA SIZE </h1>
                 {
                       cup && outPutband>27 ?
                        <h1 className='text-success fw-bold'>{outPutband}{cup}</h1>:
                        <h5 className='text-danger'>Oops! Something is wrong. Please check your measurements again</h5>
                    }
                    
                </div>
                <div>
                <span className='fs-4'>Enter Overbust: </span>
                    <input 
                    className='calculator-input'
                    type='number'
                    min="28" max="50"
                    onChange={(e)=>setBust(Math.round(e.target.value))}/>
                </div>
            </div>
            </Container>
            <Footer/>
            <MessengerChat/>
        </div>
    );
};

export default SizeCalculator;