import React from 'react';
import { Card, Col, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import swal from 'sweetalert';
import UseAuth from '../../Hooks/UseAuth';
import './ManageProduct.css'


const ManageProduct = () => {

const {allProduct,setNum}= UseAuth();

const productDltHandler=(id)=>{
    swal({
        title: "Are you sure?",
        text: "Once deleted, you will not be able to recover!",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
            const url = `https://api.femmewearbd.com/allproducts/${id}`;
            fetch(url,{
                method:'DELETE',
            })
            .then(res=>res.json())
            .then(res=>{
                //console.log(res)
                
                    swal("success! Product has been deleted!", {
                        icon: "success",
                      });
                
                setNum(num=>num+1);
            })
              
        } else {
          swal("Your Product is safe!");
        }
      });

}
    return (
        <div className='dashboard-page'> 
            {
                allProduct?.map((products)=><div key={products._id}>
                    <Card className='m-2'>
                    <Row className='m-0'>
                        <Col className='sm-5 md=3'>
                            <div className='manage-product-img'>
                                 <img   className='img-fluid' src={products.img} alt="femmewear-product" />
                            </div>
                        </Col>
                        <Col className='bg-color-red'>
                            <div className='manage-product-info'>
                                <p>Category: {products.category}</p>
                                <p>Price: {products.price}</p>
                                <p>Stock: {products.stock}</p>
                            </div>
                        </Col>
                        <Col sm={12} md={4} className='bg-color-red'>
                            <div className='manage-btn'>
                                <button className='bg-success'><Link to={`updateProduct/${products._id}`}>Update</Link></button>
                                <button 
                                    className='bg-danger'
                                    onClick={()=>productDltHandler(products._id)}>Delete</button>
                            </div>
                        </Col>
                    </Row>
                    </Card>
                </div>)
            }
        
        </div>
    );
};

export default ManageProduct;