import { useEffect, useState } from 'react';
import swal from 'sweetalert';
import { allproductsApi, bannerApi, ordersApi } from '../../API/Api';
import { getAuth, onAuthStateChanged, signInWithEmailAndPassword, signOut } from "firebase/auth";
import initializeFirebase from '../../Firebase/Firebase.init';

initializeFirebase();

const UseAuthentications = () => {
    const [orderSize, setOrderSize]=useState('')
    const [cartItems, setCartItems]=useState()
    const [allProduct, setAllProduct] = useState([]);
    const [allOrder, setAllOrder] = useState([]);
    // const [successOrder, setSuccessOrder] = useState(1);
  
    const [searchProduct, setSearchProduct] = useState([]);
    const [num,setNum] = useState(1) //delete or adding rendered

    const [isLoading, setIsLoading] = useState(true);
    const [authError, setAuthError] = useState("");
    const [admin, setAdmin] = useState(null);
    const [adminuser, setAdminUser] = useState(null);// now user means admin

  //  console.log(admin)
    // all products get method//
    useEffect(()=>{
        const url = allproductsApi
        fetch(url,)
        .then(res => res.json())
        .then(data => setAllProduct(data))
    },[allProduct]);

    // banner get method//
  const [banner,setBanneer] = useState();
  useEffect(()=>{
    fetch(bannerApi)
    .then(res=>res.json())
    .then(data=> setBanneer(data[data.length - 1]))
  },[])
 
    // ===============size pass function=========//
    const sizeHandler = (size) => {
        setOrderSize(size)
    }
    // ===============size pass function=========//
    const cartItemsHandler = (cartNum) => {
        setCartItems(cartNum)
        // //console.log(cartItems)
    }
    // =================== New order function========//
    const reload = ()=>{
        window.location.reload()
    }
    const newOrderHandler =(items,order, history)=>{
       
        const orders = [...items,order];
        const newOrder = {
            newOrder:orders
        }
        const url = ordersApi;
        fetch(url,{
            method: "POST",
            headers: {
              "content-type": "application/json",
            },
            body: JSON.stringify(newOrder),
          })
          .then(res=> res.json())
          .then(result =>{
            if(result.acknowledged){
            //   alert('Order successfull')
              swal("Success!", "Thanks for your order", "success");
              localStorage.removeItem("react-use-cart");
              setTimeout(() => {
              reload()
              }, 3000);
          }  
          })
          .then( history('/shop'));
          //   
        //   
    }
     // ==================get all order============//
     useEffect(()=>{
        const url = ordersApi;
        // https://api.femmewearbd.com/
        fetch(url)
        .then(res => res.json())
        .then(data => setAllOrder(data));
        // setSuccessOrder(successOrder+1)
    },[num]);

//================================== Admin login===============//
const auth = getAuth();

const adminLogin = (email, password, location, history) => {
  setIsLoading(true);
  signInWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      setAdmin(userCredential.user.email);
      const destination = location?.state?.from || "/";
      history(destination);
      setAuthError("");
    })
    .catch((error) => {
      setAuthError(error.message);
    })
    .finally(() => setIsLoading(false));
};
 //On auth state change-------------------------state change
 useEffect(()=>{
  const unsubscribe=onAuthStateChanged(auth, (user) => {
  if (user) {
    setAdminUser(user);
    console.log(user.email, 'user there');
  } 
  else {
    setAdminUser({})
  }
  setIsLoading(false)
    });
    return unsubscribe;
},[auth]);


// admin logout----------------------------------------//
const logout = () => {
  alert("You are going to logout!!!");
  setIsLoading(true);
  signOut(auth)
    .then(() => {
      // Sign-out successful.
    })
    .catch((error) => {
      // An error happened.
    })
    .finally(() => setIsLoading(false));
};

    return {
        allProduct,
        sizeHandler,
        orderSize,
        cartItemsHandler,
        cartItems,
        newOrderHandler,
        num,
        setNum,//for rerender after delete
        setSearchProduct,
        searchProduct,
        allOrder,
        banner,
        adminLogin,
        isLoading,
        authError,
        // admin return only admin email Address
        admin,
        adminuser,
        logout,

        // successOrder,
        // setSuccessOrder,
    }
};

export default UseAuthentications;