import React, { useEffect, useState } from 'react';
import { Button, Card, Col, Container,Form, Row } from 'react-bootstrap';
import Toast from 'react-bootstrap/Toast';
import { Link, useParams } from 'react-router-dom';
import { useCart } from 'react-use-cart';
import UseAuth from '../Hooks/UseAuth';
import Footer from '../Shared/Footer/Footer';
import MessengerChat from '../Shared/MessengerChat/MessengerChat';
import TopSection from '../Shared/TopSection';
import './ProductDetails.css'
import AdSense from '../../AdSense';
import AdSenseCardAdds from '../../AdsenseAddsCard/AdSenseCardAdds';

const ProductDetails = () => {
    const [showA, setShowA] = useState(false);
    const {detailsId} = useParams()
    // const [product, setProduct] = useState([]);
    const [size, setSize] = useState(false);
    const [errorSMSCart,setErrorSMScart] = useState('');
    const [errorSMSbuy,setErrorSMSbuy] = useState('');
    // const {cartItemsHandler} = UseAuth()
    const { totalUniqueItems} = useCart();
    const {sizeHandler,cartItemsHandler,allProduct} = UseAuth();
    useEffect(()=>{
        cartItemsHandler(totalUniqueItems)
    },[totalUniqueItems,cartItemsHandler]);

    const orderProduct = allProduct?.find((myProduct)=> myProduct?._id === detailsId);
    const productSize = orderProduct?.size;

    const cartItem = {...orderProduct, userSize:size}

    // console.log(orderProduct.category)==========================================================similler product

    const simillerProduct =  allProduct.filter(function(product) {
        return product.category === orderProduct.category;
      });
   
   sizeHandler(size)

   const {addItem} = useCart();

//    size select error msg///
const sizeErrorCart = ()=>{
 const errorMsgCart = 'Please select Size Before Add to cart';
 setErrorSMScart(errorMsgCart); 
 setErrorSMSbuy(''); 
}
const sizeErrorBuy = ()=>{
 const errorMsgbuy = 'Please select Size Before order';
 setErrorSMSbuy(errorMsgbuy); 
 setErrorSMScart(''); 
}
const toggleShowA = () => setShowA(!showA);
    return (
        <div>
            <AdSense/>
            <TopSection/>
              <div>
                <Container>
                <Row className='m-0 mt-5'>
                    <Col sm={12} md={6} lg={4} className="mt-3">
                      <div className='details-card-img'>
                        <img className='img-fluid' src={orderProduct?.img} alt="femmewear-product" />
                      </div>
                    </Col>

                    <Col sm={12} md={6} lg={3} className="mt-3">
                      <div className='product-info-section'>
                        <h3>{orderProduct?.name}</h3>
                        <p className='fs-4'><span className='fw-semibold pe-5'>Price:</span> {orderProduct?.price} ৳ </p>
                       
                        <div>
                            <div className='m-2 select-size-form'>
                                <p className='fs-4 fw-semibold pe-5'>Size:</p>
                                <Form.Select aria-label="Default select example" className='select-field' onChange={(e)=>setSize(e.target.value)}>
                                <option>Select Your Size</option>
                                {
                                    productSize?.map((size,index) =><option 
                                        key={index}
                                        value={size} 
                                        required 
                                        id="size">{size}</option>)
                                }               
                                </Form.Select>
                            
                            </div>
                            <div>  
                                {
                                    size === false?
                                    <div>
                                         <button className='addtoCart mt-2 px-2' onClick={()=>sizeErrorCart()}>Add to Cart </button> <br />
                                         <span className='text-danger'>{errorSMSCart}</span>
                                    </div>:
                                    <div>
                                        <button className='addtoCart mt-2 px-2' 
                                                onClick={()=>addItem(cartItem) || toggleShowA()}
                                                
                                                id="liveToastBtn">Add to Cart </button>
                                        <div className='cartAlert'>
                                        <Toast show={showA} onClose={toggleShowA} className='alert-body'>
                                             <Toast.Header>
                                                <strong className="me-auto">Added to Cart</strong>
                                             </Toast.Header>
                                            <Toast.Body className='text-success'>Woohoo!! Product has been added to your cart</Toast.Body>
                                            <Link to='/myCart'><Button className='bg-primary ms-2 mb-2 text-white'>View Cart</Button></Link>
                                        </Toast>
                                        </div>
                                    </div>
                                   
                                } <br />

                                {
                                    size === false?
                                    <div>
                                         <button className='buynow-btn mt-2' onClick={()=>sizeErrorBuy()}>Buy Now</button> <br />
                                         <span className='text-danger'>{errorSMSbuy}</span>
                                    </div>:
                                   <button className='buynow-btn mt-2'><Link to={`/orderForm/${orderProduct?._id}`} className='order-link'>Buy Now</Link></button>
                                   
                                }
                                {/* <button className='buynow-btn mt-2'><Link to={`/orderForm/${orderProduct?.id}`} className='order-link'>Buy Now</Link></button> */}
                            </div>
                        </div>
                      </div>
                    </Col>

                    <Col sm={12} md={12} lg={5} className="mt-3">
                        <div className='order-info-card'>
                            <p className='fs-6'>
                                <span className='fs-5 fw-semibold'>Note: </span>
                                 Shipping to Dhaka city: Tk. 70 (max 3 days)
                            </p>
                            <p className='fs-6'>
                            Shipping to other districts in Bangladesh: Tk. 140 (3-4 days)
                            </p>
                            <p className='fs-5 fw-semibold'>
                            Available payment method: <br /> Cash on delivery
                            </p>
                            <p className='fs-5 text-primary'>
                            Advance shipping cost with bKash is required for out of Dhaka city.
                            </p>
                        </div>
                    </Col>
                </Row>
                <div className='product-des'>
                    <h3>Description:</h3>
                    <p>
                       {orderProduct?.description} 
                    </p>
                </div>



                {/* Google adds// */}
                <AdSenseCardAdds/>


{/* see similler product// */}
           <div className='text-start py-5'>
           <h3>Similler Product you may like:</h3>
           </div>
            <Row xs={2} md={4} className="g-4">
               {simillerProduct?.map((braset,index) => (
                  <Col key={index}>
                     <Card className='product-card'>
                      <Link to={`/details/${braset._id}`} className='order-link'>
                        <Card.Img variant="top" src={braset.img} className="img-fluid" />
                        </Link>
                            <div className='text-start product-size pb-2'>
                            <div>  
                                 <div className='card-price'>
                                    <p>Available size:</p><p className='bdt-show'>&#2547; {braset?.price}</p>
                                 </div>
                                 <div className='size-box'>
                                    {
                                      braset.size.map((sizes,index)=><span key={index}>{sizes}</span>)
                                     }
                                 </div>
                            </div>
                            <button className='buynow-btn mt-2'><Link to={`/details/${braset._id}`} className='order-link'>Order Now</Link></button>
                            </div>
                                 {/* <button className='addtoCart mt-2' onClick={()=>addItem(braset)}>Add to Cart</button> */}
                               
                        </Card>
                  </Col>
              ))}
              </Row>

                    {/* Google adds// */}
                    <AdSenseCardAdds/>

                </Container>
              </div>
            <Footer/>
            <MessengerChat/>
        </div>
    );
};

export default ProductDetails;

