import React from 'react';
import {Link} from 'react-router-dom';
import logo from '../../../Assets/femmlogo4.png'
import './DashboardNav.css';
import { Button } from 'react-bootstrap';
import UseAuth from '../../Hooks/UseAuth';
const DashboardNav = () => {
  const {admin,logout} = UseAuth()
    return (
        <div>
            <div className='dashboard-topbar'>
                {/* <NavLink to='/'>
                  <button className='backtoSite-btn'>Back to the site</button>
                </NavLink> */}
  
                <Button className='pt-3'>{admin}</Button>
       
            </div>
            <div>
      <nav role="navigation">
        <div id="menuToggle">
          <input className='manu-check-btn' type="checkbox" id='select-icon' defaultChecked/>
          <label htmlFor="select-icon">
           <div className='dashboard-bar-icon' id='three-bar'>
                 <i className="fa-solid fa-bars"></i>
           </div>
          </label>
       
          <i className=""></i>
          <ul id="menu">
              <div className='dashboard-logo'>
              <img className='ms-5 ' src={logo} alt="femmewear-product" />
              </div>
              <Link to=''><li><i className="fa-solid fa-cube nav-icon-style"></i>Manage Order</li></Link>
              <Link to='manageProduct'><li><i className="fa-solid fa-list-check nav-icon-style"></i>Manage Product</li></Link>
              <Link to='addProduct'><li><i className="fa-solid fa-add nav-icon-style"></i>Add Product</li></Link>
              <Link to='addBnnerText'><li><i className="fa-solid fa-file  nav-icon-style"></i>Add Bnner</li></Link>
              <Link to='addCupon'><li><i className="fa-solid fa-percent  nav-icon-style"></i>Add Cupon</li></Link>
              <Link to='addBlogs'><li><i className="fa-solid fa-pen-to-square  nav-icon-style"></i>Post A Blog</li></Link>
              <Link to='sellInfo'><li><i className="fa-solid fa-table-cells-large  nav-icon-style"></i>Selling Info</li></Link>
              {/* <Link to='ImageUploader'><li><i className="fa-solid fa-user  nav-icon-style"></i>ImageUploader</li></Link> */}
              <Link to='/'><li><i className="fa-solid fa-house  nav-icon-style"></i>Go to Shop</li></Link>
              <p>Setteings</p>
              {/* <Link to=''><li><i className="fa-solid fa-box  nav-icon-style"></i>Shop Category</li></Link> */}
              <Link to='deliveryStatus'><li><i className="fa-solid fa-money nav-icon-style"></i>Delivery Status</li></Link>
              <Link to=''><li><i className="fa-solid fa-users nav-icon-style"></i>Customer </li></Link>
              <Link to=''><li><i className="fa-solid fa-share-nodes nav-icon-style"></i>Social Links</li></Link>
              <Link to=''><li><i className="fa-solid fa-location nav-icon-style"></i>Update Footer</li></Link>
              <Link to=''><li><i className="fa-solid fa-tag nav-icon-style"></i>Update Logo</li></Link>
            {/* </div> */}
            <div className='last-text text-white'>
               Admin logout
               <Button className='btn btn-danger' onClick={logout}>
                  logout
               </Button>
            </div>
            
          </ul>

        </div>
      </nav>
    </div>
        </div>
    );
};

export default DashboardNav;