import React from 'react';
import TopSection from '../Shared/TopSection';
import Footer from '../Shared/Footer/Footer';
import MessengerChat from '../Shared/MessengerChat/MessengerChat';
import { Container } from 'react-bootstrap';
import './policy.css'

const PrivacyAndPolicy = () => {
    return (
        <div>
            <TopSection/>
                <Container>
                    <div className='policy-wraper'>
                        <h4>Privacy and Policy</h4>

                        <h5>Data Policy</h5>
                     <p>
                         Welcome to the femmewearbd.com website (the "Site") operated by Femme Wear BD. <br />
                         We respect your privacy and want to protect your personal information. <br /> 
                         We respect the privacy of our customers. We Shall not share your personal information, name, address, mobile number etc. with anyone or any third party which is not related with Femme Wear BD. <br />
                         We will cease to retain your personal data, or remove the means by which the data can be associated with you, as soon as it is reasonable to assume that such retention no longer serves the purposes for which the personal data was collected, and is no longer necessary for any legal or business purpose. 
                    </p>

                    <h5>Return Policy</h5>
                    <p>
                    </p>
                    <h5>Terms of Use</h5>
                   <p>

                   </p>

                </div>
             </Container>
               
            <Footer/>
            <MessengerChat/>
            
        </div>
    );
};

export default PrivacyAndPolicy;