import React, { useEffect } from 'react';
import { useState } from 'react';
import { Card, Col, Container, Row } from 'react-bootstrap';
import loder from '../../../Assets/Ripple-1s-200px.svg'
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import Footer from '../../Shared/Footer/Footer';
import MessengerChat from '../../Shared/MessengerChat/MessengerChat';
import TopSection from '../../Shared/TopSection';
import './Shop.css'
import AdSense from '../../../AdSense';
import AdSenseCardAdds from '../../../AdsenseAddsCard/AdSenseCardAdds';

const Shop = () => {
    const {searchProduct, allProduct} = UseAuth();

    const [allProducts, setAllProducts] = useState();
    const [products, setProducts] = useState()

    const [imagesLoaded, setImagesLoaded] = useState(false);

    useEffect(()=>{
        const url = "https://api.femmewearbd.com/allProducts";
        fetch(url)
        .then(res => res.json())
        .then(data => setAllProducts(data))
    },[]);
 
    useEffect(()=>{
        const url = "https://api.femmewearbd.com/allProducts";
        fetch(url)
        .then(res => res.json())
        .then(data => setProducts(data))
    },[]);

    useEffect(()=>{
            setProducts(searchProduct); 
    },[searchProduct]);
    

    const shortingHandler = (value)=>{
        const category = allProduct?.filter((product) => {
            return (product?.category === value)
          });
          setProducts(category)
    }
    const allCategory = ["Bra Set","Padded","Non Padded", "Wireless", "Sports",
                         "BodySuit/S.Suit", "Bikini", "Teenager Bra", "Mother Bra", "Lingerie", 
                         "Panty", "Nighty", "T-shirt", "Shorts", "Skirts","Menstrual Cup",
                         "Sanitary Napkin"]
    return (
        <div>
            <AdSense/>
              <TopSection/>
              <section>
                <Row className='m-0 '>
                    {/* short by category/ */}
                    <Col className="col-4 col-md-2 p-0">
                       <aside className='sort-category'>
                        {/* -----------------------------search by category select in side nav only for desktop//
                        ------------------------------------------------------------ */}
                        <div className="select-form">
                            <div className='category-wraper'>
                            <div className="select-form">
                                <button
                                    className="search-category"
                                    onClick={()=>setProducts(allProducts)}
                                >
                                    All Product
                                </button>
                                {/* <label htmlFor='allCategory' className="ms-1 ms-md-2 fw-semibold">All Product</label> */}
                            </div>
                                {
                                      allCategory?.map((itemsType,index)=><div   
                                      className="select-form"
                                      key={index}
                                           >
                                         <button
                                            name={itemsType} 
                                            value={itemsType}
                                            className="search-category"
                                            onClick={(e)=>shortingHandler(e.target.value)}
                                        >
                                        {itemsType}
                                        </button>
                                        </div>
                                 )}
                            </div>
                     </div>
                         
                       </aside>
                    </Col>

                    {/* product */}
                    <Col className='col-12 col-md-10 shop-wraper'>
                        <div>
                        <Container>
                            <Row xs={2} md={4} className="g-3 m-0">
                            {products?.length >=1?  
                                products?.map((product, index) => (
                                 <div key={index}>
                                    <Card className='product-card'>
                                        {/* <Card.Img variant="top" src={allProduct.img} className="img-fluid" /> */}
                                       
                                        <Link to={`/details/${product._id}`} className='order-link'> 
                                        <Card.Img
                                          variant="top"
                                          src={product.img}
                                          className="img-fluid product-card-img"
                                          onLoad={() => setImagesLoaded(true)}
                                          style={!imagesLoaded ? { display: 'none' } : {}}
                                        /></Link>

                                        <div className='text-start product-size pb-2'>
                                            <div >
                                                <div className='card-price'>
                                                    <p>Available size:</p><p className='bdt-show'>&#2547; {product?.price}</p>
                                                </div>
                                            
                                            <div className='size-box'>
                                            {
                                              product.size.map((sizes,index)=><span key={index} >{sizes}</span>)
                                            }
                                            </div>
                                            </div>
                                            <button className='buynow-btn mt-2'>
                                        <Link to={`/details/${product._id}`} className='order-link'>Order Now
                                        </Link>
                                        </button>
                                        </div> 
                                        {/* <button className='addtoCart mt-2'>Add to Cart</button> */}
                                        
                                    </Card>
                                    </div> 
                                ))
                                : 
                                <div className='shop-loader'>
                                    <img src={loder} alt="loader" />
                                </div>
                             } </Row>


                              {/* Google adds// */}
                                <AdSenseCardAdds/>
                            </Container>
                        </div>
                    </Col>
                </Row>
              </section>
              <div className='shop-footer'><Footer/></div>
              <MessengerChat/>
        </div>
    );
};

export default Shop;