import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import swal from 'sweetalert';
// import UseAuth from '../../Hooks/UseAuth';
import './DeliveryStatus.css'

const DeliveryStatus = () => {
    
    // const {setNum,num} = UseAuth();
    const [statusOrder, setStatusOrder] = useState()
    const [queryOrder, setQueryOrder] = useState()
    useEffect(()=>{
        const url = 'https://api.femmewearbd.com/orderStatus';
        fetch(url)
        .then(res=>res.json())
        .then(data => setStatusOrder(data) || setQueryOrder(data));
        
    },[])
    // eslint-disable-next-line no-extend-native
    Array.prototype.last = function(){
        return this[this.length - 1];
    }

const statusHandler =(status)=>{
    const successQuery = queryOrder?.filter((success)=> success.deliveryStatus === status);
    setStatusOrder(successQuery);
    if(status === 'all'){
        setStatusOrder(queryOrder);
    }
}

const deleteHandler = (id)=>{
    console.log(id)
        swal({
            title: "Are you sure?",
            text: "Once deleted, you will not be able to recover!",
            icon: "warning",
            buttons: true,
            dangerMode: true,
          })
          .then((willDelete) => {
            if (willDelete) {
                // const url = `https://api.femmewearbd.com/deliveryStatus/${id}`;
                const url = `https://api.femmewearbd.com/deliveryStatus/${id}`;
                fetch(url, {
                method: "DELETE",
                })
                .then((res) => res.json())
                .then((data) => {
                    if (data.deletedCount > 0) {
                        swal("success! Order has been deleted", {icon: "success",});
                   // setNum(num=>num+1)
                    }
                });
            } 
            else {
              swal("Your Product is safe!");
            }
          });     
}


    return (
        <div className='dashboard-page'>
            <div className='status-btn'>
                <button onClick={()=>statusHandler('all')}> All </button>
                <button onClick={()=>statusHandler('success')}> Success Order</button>
                <button onClick={()=>statusHandler('return')}> Return Order</button>
            </div>
            <div className='table-container'>
         <table className='status-table'>
            <tbody>
            <tr>
                <th>Customer</th>
                <th>Location</th>
                <th>PhoneNumber</th>
                <th>Address</th>
                <th>Amount</th>
                <th>Status</th>
                <th>Delete</th>
            </tr>
            {
                statusOrder?.map((list,index)=><tr key = {index}>
                    <td className='p-2'>{list.newOrder.last().userName}</td>
                    <td className='p-2'>{list.newOrder.last().district}</td>
                    <td className='p-2'>{list.newOrder.last().phoneNumber}</td>
                    <td className='p-2'>{list.newOrder.last().address}</td>
                    <td className='p-2'>{list.newOrder.last().payableAmount}</td>
                    {
                        list.deliveryStatus === 'return'?
                        <td className='p-2 bg-danger text-white'>{list.deliveryStatus}</td>:
                        <td className='p-2 bg-success text-white'>{list.deliveryStatus}</td>
                    }
                    <td> 
                     <button 
                     className='text-danger border-0'
                     onClick={()=>deleteHandler(list._id)}>
                        <i className="fa-solid fa-trash"></i>
                    </button>
                     </td>
                    
                </tr>)
            }
            </tbody>
         </table>
         </div>
        </div>
    );
};

export default DeliveryStatus;