// import { Axios } from 'axios';
import React from 'react';
import { useState } from 'react';
import { Card, Container } from 'react-bootstrap';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { useFieldArray, useForm, } from 'react-hook-form';
import UseAuth from '../../Hooks/UseAuth';
import swal from 'sweetalert';
import './AddProduct.css'


const AddProduct = () => {

  const [selectedFile, setSelectedFile] = useState();
  const [uploadProgress, setUploadProgress] = useState(0);
  const [productImg,setProductImg] = useState(null);

  const {setNum} =UseAuth();


  const {register, handleSubmit,control,reset} = useForm();

  // product data add to the database(mongoDB)/
  const onSubmit = data =>{
    const size = data.sizeObject?.map(value=>{return value.value});
    // product data model//
    const product = {
      category:data.category,
      description:data.description,
      img:productImg,
      name:data.name,
      objectType:'product',
      price:data.price,
      stock:data.stock,
      size:size,
      id:Math.floor(Math.random() * 1000000000)
      }
    
    fetch("https://api.femmewearbd.com/allproducts", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(product),
    })
    .then(res=> res.json())
    .then(result =>{
      if(result.acknowledged){
        setNum(num=>num+1)
        swal("Success!", "Product successfully Added", "success");
    }
    reset();
    } )
  };

// image uploader handler//
const now = 100;
const handleImageUpload = (event) => {
  const file = event.target.files[0];
  setSelectedFile(file);

  // Create a new FormData object
  const formData = new FormData();
  formData.append('image', file);

  // Make a POST request to the backend endpoint for file upload
  fetch('https://api.femmewearbd.com/uploadProductImg', {
    method: 'POST',
    body: formData,
    // Report upload progress
    onUploadProgress: (progressEvent) => {
      const progress = Math.round(
        (progressEvent.loaded / progressEvent.total) * 100
      );
      setUploadProgress(progress);
    }
  })
    .then((response) => response.json())
    .then((data) => {
      
      console.log('File uploaded successfully', data);
      const Imgurl = data.imageUrl;
      // const filename = Imgurl.substring(Imgurl.lastIndexOf('/') + 1);
      // get image url after upload
      const url = Imgurl
      setProductImg(url)
      console.log(url)
    })
    .catch((error) => {
      console.error('Error uploading file', error);
      // Handle error if the file upload fails
    });
    //console.log(selectedFile);
    event.preventDefault() 

};


    // use field for multi form//
    const { fields, append, remove} = useFieldArray({
      control, // control props comes from useForm (optional: if you are using FormContext)
      name: "sizeObject", // unique name for your Field Array
    });
    const allCategory = ["Bra Set","Padded","Non Padded", "Wireless", "Sports",
    "BodySuit/S.Suit", "Bikini", "Teenager Bra", "Mother Bra", "Lingerie", 
    "Panty", "Nighty", "T-shirt", "Shorts", "Skirts","Menstrual Cup",
    "Sanitary Napkin"]


    return (
        <div className='dashboard-page'>
            <h3>Add Your Product</h3>
            <Container>
                <Card>
                  <form onSubmit={handleSubmit(onSubmit)}> <br />
                      <div className='product-add-form'>
                      <input {...register("name", { required: true})} 
                        placeholder='Enter product Name'
                        className='add-in-field' />
                        <input type='file' {...register("img",{required:true})}
                        className='add-in-field'
                        onChange={(e) => handleImageUpload(e)}
                        />
                        <div className='add-in-field'>
                          {
                            productImg || selectedFile?<ProgressBar now={now} label={`${now}%`} />:<ProgressBar now={0} label={`${uploadProgress}%`} />
                          }
                        </div>
                        <select {...register("category",{required:true})}
                        className='add-in-field'>
                            <option >Select Category</option>
                            {
                              allCategory.map((itemsType,index)=><option 
                                key={index}
                                value={itemsType}>
                                {itemsType}
                              </option>)
                            }
                        </select>
                      
                        <input {...register("price", {required: true})} 
                        type='number'
                        placeholder='Give the price'
                        className='add-in-field'/> 
                        <textarea {...register("description", {required: true})} 
                        rows='4'
                        placeholder='description'
                        className='add-in-field'/>
                        <input type='number' {...register("stock",{required:true})} 
                        placeholder='stock'
                        className='add-in-field'/>
                        <div className='multi-form'>
                        <span>Available size:</span> <br />
                        {fields.map((field, index) => (
                          <input
                            key={field.id} // important to include key with field's id
                            className='size-field'
                            {...register(`sizeObject.${index}.value`,{required:true})} 
                          />
                        ))}  
                        <button 
                        type='button'
                          onClick={()=>append()}
                          className='add-btn' 
                        > +</button>          
                        </div>
                        <div className="size-clear-btn-container">
                          <button type='button' className='size-clear-btn' onClick={()=>remove()}> Remove Size</button>
                        </div>
                        
                        <input type="submit" className='submite-btn'/>
                      </div>
                      
                  </form>
                </Card>
            </Container>
          
        </div>
    );
};

export default AddProduct;

// 