import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { Card, Col, Container, Row } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import './OrderDetails.css'

const OrderDetails = () => {
    const {orderId} = useParams();
    const [products, setProducts] = useState()
    const {allOrder} = UseAuth();
    
    const thisOrder = allOrder.find((filterOrder)=> filterOrder._id === orderId);
    const orderArry = thisOrder?.newOrder;
   
   useEffect(()=>{
    setProducts(orderArry?.slice(0, -1));
   },[orderArry]);

    const customer = (orderArry?.[orderArry?.length-1]);

    const shippingHandler=(status,id)=>{
        const url = "https://api.femmewearbd.com/order/status";
         fetch(url,{
           method:'PUT',
           headers:{
            'content-type':"application/json"
          },
          body:JSON.stringify( {id:id,status:status})
        })
        .then(res=>{
            alert('order ready for delevery');
            window.location.reload()
        })
        
    }

    return (
        <div className='dashboard-page'>
            <Container>
                <Row>
                    <Col className='col-md-4'>
                        {
                          products?.map((product,index)=><Card key={index} className='my-3'>
                            
                            <div className='order-product'>
                               <img className='ordered-img' src={product.img} alt="femmewear-product" />
                               <div className='product-de'>
                                 {product.name} <br />
                                 size: {product.userSize} <br />
                                 Quantity: {product.quantity} <br />
                                 <p>id:{product.id}</p>
                               </div>
                            </div>
                          </Card>
                          )
                        }
                    </Col>
                    <Col className='col-md-8'>
                        <Card className='m-3 text-start p-2 ps-md-4'>
                            <p>Customer Name: {customer?.userName}</p>
                            <p>PhoneNumber: {customer?.phoneNumber}</p>
                            <p>District: {customer?.district}</p>
                            <p>Address: {customer?.address}</p>
                            <h3>Total Payable: {customer?.payableAmount}</h3>
                            <div>
                                {
                                    thisOrder?.orderStatus === "shipping"?<span
                                    className='shipping-done'>
                                        Shipping
                                    </span>:
                                    <button 
                                    className='ready-for-shipping'
                                    onClick={()=>shippingHandler("shipping",orderId)}>
                                        Ready for delivery
                                    </button>
                                }
                                
                            </div>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default OrderDetails;