import React from 'react';
import { Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import './OrderManagement.css'

const OrderManagment = () => {

    const {allOrder, setNum} = UseAuth();

    const returnHandlar = (value, condition) =>{
      
       const returnOrder = allOrder?.find((returns)=>returns?._id === value);
       if(condition === 'return'){
           const returnProduct = {...returnOrder, deliveryStatus:'return'};
           fetch("https://api.femmewearbd.com/orderStatus", {
            method: "POST",
            headers: {
                "content-type": "application/json",
            },
            body: JSON.stringify(returnProduct),
            })
            .then(res=> res.json())
            .then(result =>{
            if(result.acknowledged){
                alert('product now status list as a return')
            }
            // setNum(num=>num+1);
            } )
       }
       if(condition === 'success'){
           const successProduct = {...returnOrder, deliveryStatus:'success'};
        //    setStatus(successProduct);
        fetch("https://api.femmewearbd.com/orderStatus", {
            method: "POST",
            headers: {
                "content-type": "application/json",
            },
            body: JSON.stringify(successProduct),
            })
            .then(res=> res.json())
            .then(result =>{
            if(result.acknowledged){
                alert('product now status list as a success')
            }
            // setNum(num=>num+1)
            } )
       }
 //    ---------------------order store to status order list-------
            // ----------------------status order delete from new order--
            const url = `https://api.femmewearbd.com/order/${value}`;
            fetch(url,{
                method:'DELETE'
            })
            .then(res=>res.json())
            .then(data=>{
                if(data.acknowledged){
                   alert('successfully delete')
                }
                setNum(num=>num+1)
            })
           
    }

    return (
        <div className='dashboard-page'>
            <div>
                <Container>
                <table style={{width:'100%'}} className='orderTable'>
                    <tbody>
                    <tr className='table-row'>
                        <th className='table-heading'>Order Id</th>
                        <th className='table-heading'>Make Status</th>
                        <th className='table-heading'>Details</th> 
                    </tr>
                    {
                        allOrder?.map((order,index)=><tr key={index}>
                            <td>
                                {
                                    order._id.slice(17,25)
                                }
                            </td>
                            <td>
                               {order?.orderStatus?
                               <button className='shipping'>Pedding</button>: 
                               <button className='new-order'>New</button>
                               }

                                <button 
                                className='return'
                                onClick={()=>returnHandlar(order?._id,"return")} >Return</button>
                                <button 
                                className='success'
                                onClick={()=>returnHandlar(order?._id,"success")}>Success</button>
                            </td>
                            <td>
                            <button className='details'>
                                <Link to={`order/${order?._id}`}>
                                    Details
                                </Link>
                            </button>
                            </td>
                        </tr>)
                    }
                    </tbody>
                </table>
                </Container>
            </div>
          
        </div>
    );
};

export default OrderManagment;