import React, { useEffect, useState } from 'react';
import { Card, Col,Container, Row, } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import UseAuth from '../Hooks/UseAuth';
import TopSection from '../Shared/TopSection';
import { useNavigate } from 'react-router-dom';
import './PlaceOrder.css'
import MessengerChat from '../Shared/MessengerChat/MessengerChat';
import AdSense from '../../AdSense';

const PlacedOrder = () => {
    const {orderId} = useParams()
    const [userCoupon, setUserCoupon] = useState('');
    const [quantity, setQuantity] = useState(1);
    const [totalPrice, setTotalPrice] = useState(1);
    const [disError, setDisError] = useState();
    const [orderProductPrice, setorderProductPrice] = useState();

    const [district, setDistrict] = useState('none');
    const [userName, setUserName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [userAddress, setUserAddress] = useState('');

    const {newOrderHandler, orderSize, allProduct} = UseAuth();
    // --------------------------------------------product data//
    const orderProduct = allProduct?.find((myProduct)=> myProduct?._id === orderId);


    // Set delivery cost based on the selected district
        let cost = 0;
        if (district === 'Dhaka') {
        cost = 70;
        } else if (district !== 'none') {
        cost = 140;
        }
     //   !important-------------------------total calculation
    //  const totalCosting = totalPrice + (district === 'Dhaka'?70:140 || district === 'none'? 0 :cost);
     const totalCosting = totalPrice + cost;

     // ------------discount price-------//
     const discount = 10;
     const discountprice = (discount/100)*totalCosting;
     const costWithDiscount = totalCosting - discountprice;
     //----------coupon code--------------//
     const adminCopun = 'femmewearbd10'

     const payableAmount = adminCopun === userCoupon? costWithDiscount :totalCosting;

      //-----------------order details//
      const order = {
        userName: userName,
        phoneNumber:phoneNumber,
        district:district,
        address:userAddress,
        payableAmount:payableAmount,

      }
      const items = [
        {
            img:orderProduct?.img,
            name: orderProduct?.name,
            quantity:quantity,
            userSize:orderSize,
            objectType: "product"
        }
      ]
    
    useEffect(()=>{
        setorderProductPrice(parseInt(orderProduct?.price));
        const totalPrices = orderProductPrice*quantity;
        setTotalPrice(totalPrices);
  
    },[orderProduct?.price,quantity,orderProductPrice,totalPrice])


    const plusQuantity = (size)=>{
        const plus = document.getElementById('quantityValue');
        if (quantity >= 10) {
            return plusQuantity;
        }
        const totalQuantity =  plus.value = quantity+1;
        setQuantity(totalQuantity);
    }


    const minusQuantity = ()=>{
        const plus = document.getElementById('quantityValue');
        if (quantity <= 1) {
            return minusQuantity;
        }
        const totalQuantity =  plus.value = quantity-1;
        setQuantity(totalQuantity)  
    }

const districtErrorHadler =()=>{
    setDisError('Please select Your District')
}

// --------------------------submit handler///
const history = useNavigate()
const submitHandler =(e,items,order)=>{
    e.preventDefault();
    newOrderHandler(items,order,history)
    // e.preventDefault();
}

const allDistrict = ["Dhaka","Faridpur","Gazipur","Gopalganj","Jamalpur","Kishoreganj","Madaripur","Manikganj","Munshiganj","Mymensingh","Narayanganj","Narsingdi","Netrokona","Rajbari","Shariatpur","Sherpur","Tangail","Bogura","Joypurhat","Naogaon","Natore","Nawabganj","Pabna","Rajshahi","Sirajgonj","Dinajpur","Gaibandha","Kurigram","Lalmonirhat","Nilphamari","Panchagarh","Rangpur","Thakurgaon","Barguna","Barishal","Bhola","Jhalokati","Patuakhali","Pirojpur","Bandarban","Brahmanbaria","Chandpur","Chattogram","Cumilla","CoxBazar","Feni","Khagrachari","Lakshmipur","Noakhali","Rangamati","Habiganj","Maulvibazar","Sunamganj","Sylhet","Bagerhat","Chuadanga","Jashore","Jhenaidah","Khulna","Kushtia","Magura","Meherpur","Narail","Satkhira"]
    return (
        <div>
            <AdSense/>
            <TopSection/>
            <Container>
                <form onSubmit={(e)=>submitHandler(e,items,order)}>
                    <Row>
                        <Col sm={12} md={6}>
                          <Card className='my-4 pb-2 item-card'>
                             <Row className='p-2 text-start row-cols-sm-2'>
                                 <Col className='col-sm-4 img-product'>
                                   <img className='img-fluid' src={orderProduct?.img} alt="femmewear-product" />
                                </Col>
                                    
                                <Col className=' text-start fs-5 fw-bold'>
                                    <h4>{orderProduct?.name}</h4>
                                    <h5>Price: {orderProduct?.price} BDT</h5>   
                                   <h6>Size:{orderSize} </h6> 
                               </Col>
                               <Col sm={6} md={6} lg={4}>
                                   <span className='me-2 fs-5 fw-bold'>Quantity : <input type="number" value={quantity} id="quantityValue" required /></span> <br />
                                   <button type='button'  className='update-btn' onClick={()=>minusQuantity()}>-</button>
                                   <button type='button' className='update-btn' onClick={()=>plusQuantity()}>+</button>     
                                  
                               </Col>
                            </Row>                             
                        </Card>        
                       </Col>

                        <Col sm={12} md={6}>
                           <Card className='my-4'>
                            <div className='px-2 py-4'>
                            <Row className='text-start'>
                               <Col sm={12} md={6} className="my-2">
                                <div className='input-key-value'>
                                <span className='order-uerser-key'>District:</span>
                                    <select 
                                    name="bd_districts" 
                                    onChange={(e)=>setDistrict(e.target.value)} 
                                    required
                                    className='user-input'>
                                        <option value="none" selected >Select your Districts</option>
                                            {
                                                allDistrict.map(bddistrict =><option value={bddistrict}>{bddistrict}</option>)
                                            }
                                    </select>
                                </div>
                                </Col>
                                <Col sm={12} md={6}>{
                                    <p> Delivery Cost: {cost} taka</p>
                                }
                                  
                                </Col>
                                <p className='text-danger'>{disError}</p>
                                <Col sm={12}>
                                <div className='input-key-value'>
                                     <span className='order-uerser-key'>Address:</span>
                                     <input 
                                     type="text" 
                                     placeholder='house, road, area' 
                                     required 
                                     className='shipping-address-field user-input'
                                     onChange={(e)=>setUserAddress(e.target.value)} />
                                </div>
                                </Col>
                                <Col sm={12} className="my-2">
                                <div className='input-key-value'> 
                                <span className='order-uerser-key'>Name: </span>
                                    <input 
                                    type='text' 
                                    placeholder='Enter Your Name' 
                                    className='user-input'
                                    required onChange={(e)=>setUserName(e.target.value)}/>
                               </div>
                                </Col>
                                <Col sm={12} className="">
                                <div className='input-key-value'> 
                                <span className='order-uerser-key'>Phone : </span>
                                    <input 
                                    type='number' 
                                    className='user-input'
                                    placeholder='Enter Contact Number' 
                                    required onChange={(e)=>setPhoneNumber(e.target.value)}/>
                                </div>
                                </Col>
                                <Col sm={12}>
                                    <br />
                                   <h5 className='text-primary'> Payable Amount :BDT {payableAmount} &#2547;</h5> <br />
                                   <h4>Discount Coupon :</h4>
                                   <input type="text"  className='cuponfield' onChange={(e)=>setUserCoupon(e.target.value)} placeholder='coupon code'/> <br /> <br />
                                </Col>
                                <div className='submit-btn-container'>
                                       {
                                        district === 'none'? 
                                        <p className='submit-btn text-center py-1' onClick={()=>districtErrorHadler()}>
                                            Submit
                                        </p>:
                                        <input className='submit-btn' type="submit" />
                                       } 
                                </div>
                               </Row>
                            </div>
                            
                           </Card>
                        </Col>
                    </Row>
                </form>
            </Container>
            <MessengerChat/>
        </div>
    );
};

export default PlacedOrder;

    
